@import url(https://fonts.google.com/specimen/Poppins);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --red: hsl(0, 78%, 62%);
  --cyan: hsl(180, 62%, 55%);
  --orange: hsl(34, 97%, 64%);
  --blue: hsl(212, 86%, 64%);

  --very-dark-blue: hsl(234, 12%, 34%);
  --grayish-blue: hsl(229, 6%, 66%);
  -very-light-gray: hsl(0,0%, 98%);
}

* {
  padding:0;
  margin: 0;
  box-sizing: border-box;
}

body {
  background-color: var(--very-light-gray);
}

.App {
  font-family: "Poppins", sans-serif;
  text-align: center;
  color: hsl(229, 6%, 66%);
  color: var(--grayish-blue);
}

.Header > h1 {
  padding-top: 4rem;
  padding-bottom: 1.5rem;
  font-weight: normal;
}

.Header > h1 > span {
 font-weight: bold;
 color: hsl(234, 12%, 34%);
 color: var(--very-dark-blue);
}

.card-container {
  margin-top: 4rem;
  text-align: left;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.box {
  background-color: #fff;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.05);
  border-radius: 10px;
  padding: 30px;
  position: relative;
  margin: 15px;
  width: 350px;
  height: 250px;
}

.box > h2 {
  padding-bottom: 20px;
  color: hsl(234, 12%, 34%);
  color: var(--very-dark-blue);
}

.box > p {
  padding-bottom: 40px;
}

.box > img {
  display: block;
  margin-left: 12rem;
}

.box::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 5px;
  width: 100%;
}

.box-supervisor > .image {
  background-image: url(/four-card/static/media/icon-supervisor.2dd10a2d.svg);
  background-repeat: no-repeat;
  width: 65px;
  height: 65px;
  position: absolute;
  left: 15rem;
}

.box-supervisor::after {
  background-color: hsl(180, 62%, 55%);
  background-color: var(--cyan);
}

.box-team-builder > .image {
  background-image: url(/four-card/static/media/icon-team-builder.b885fb58.svg);
  background-repeat: no-repeat;
  width: 65px;
  height: 65px;
  position: absolute;
  left: 15rem;
}

.box-team-builder::after {
  background-color: hsl(0, 78%, 62%);
  background-color: var(--red);
}

.box-karma > .image {
  background-image: url(/four-card/static/media/icon-karma.24e0debe.svg);
  background-repeat: no-repeat;
  width: 65px;
  height: 65px;
  position: absolute;
  left: 15rem;
}

.box-karma::after {
  background-color: hsl(34, 97%, 64%);
  background-color: var(--orange);
}

.box-calculator > .image {
  background-image: url(/four-card/static/media/icon-calculator.21bc9adc.svg);
  background-repeat: no-repeat;
  width: 65px;
  height: 65px;
  position: absolute;
  left: 15rem;
}

.box-calculator::after {
  background-color: hsl(212, 86%, 64%);
  background-color: var(--blue);
}

.box-change {
  transform: translateY(50%);
}

@media(max-width: 1024px) {
  .box-change {
    transform: translateY(0);
  }
}














